html,
body {
  width: 100%;
  height: 100%;
}

* {
  margin: 0;
  padding: 0;
}

body {
  font-family: -apple-system, SF UI Text, Helvetica Neue, Helvetica, Arial, sans-serif;
  margin: 0;
  padding: 0;
  color: #000;
  line-height: 1.4;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
      text-size-adjust: 100%;
  background: #fff;
  -webkit-user-select: none;
  /* 禁止选中文本（如无文本选中需求，此为必选项） */
  -moz-user-select: none;
   -ms-user-select: none;
       user-select: none;
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
}

body div,
body dl,
body dt,
body dd,
body ul,
body ol,
body li,
body h1,
body h2,
body h3,
body h4,
body h5,
body h6,
body pre,
body form,
body fieldset,
body input,
body p,
body a,
body blockquote,
body th,
body i,
body u,
body span {
  margin: 0;
  padding: 0;
  font-weight: normal;
}

body ol,
body ul {
  list-style: none;
}

body u,
body i {
  font-weight: normal;
  font-style: normal;
}

body a {
  color: #000;
  text-decoration: none;
}

body a,
body img {
  -webkit-touch-callout: none;
  /* 禁止长按链接与图片弹出菜单 */
}

body input,
body select,
body button,
body textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  -o-appearance: none;
  border: none;
  background-color: #fff;
  margin: 0;
  padding: 0;
  resize: none;
  outline: none;
  border-radius: 0;
}

body table {
  border-collapse: collapse;
  border-spacing: 0;
}

body .clearfix {
  *zoom: 1;
}

body .clearfix:after {
  display: block;
  overflow: hidden;
  clear: both;
  height: 0;
  visibility: hidden;
  content: ".";
}

body input[type="checkbox"] {
  -webkit-appearance: checkbox;
             -moz-appearance: checkbox;
          appearance: checkbox;
  box-sizing: border-box;
}

body input[type="radio"],
body input[type="checkbox"] {
  background-color: initial;
  padding: initial;
  border: initial;
}

body input {
  -webkit-appearance: textfield;
  background-color: white;
  -webkit-rtl-ordering: logical;
  -webkit-user-select: text;
             -moz-user-select: text;
              -ms-user-select: text;
          user-select: text;
  cursor: auto;
  padding: 0.016667rem;
  border-width: 0.033333rem;
  border-style: inset;
  border-color: initial;
       -o-border-image: initial;
          border-image: initial;
}

body input,
body textarea,
body keygen,
body select,
body button {
  text-rendering: auto;
  color: initial;
  letter-spacing: normal;
  word-spacing: normal;
  text-transform: none;
  text-indent: 0;
  text-shadow: none;
  display: inline-block;
  text-align: start;
  margin: 0;
  font: 0.222222rem Arial;
}

body input,
body textarea,
body keygen,
body select,
body button,
body meter,
body progress {
  -webkit-writing-mode: horizontal-tb;
}

@-webkit-keyframes maskin-animations {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes maskin-animations {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@-webkit-keyframes maskout-animations {
  0% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

@keyframes maskout-animations {
  0% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

body #app {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

