.mui-switch,
.mui-switch-cp__box {
  position: relative;
  width: 1.2rem;
  height: 0.533333rem;
  outline: 0;
  border-radius: 0.266667rem;
  box-sizing: border-box;
  background-color: #DFDFDF;
  transition: background-color 0.1s, border 0.1s;
  -webkit-appearance: none !important;
             -moz-appearance: none !important;
          appearance: none !important;
}

[data-dpr="1"] .mui-switch,
[data-dpr="1"] .mui-switch-cp__box {
  border: 0.5px solid #DFDFDF;
}

[data-dpr="2"] .mui-switch,
[data-dpr="2"] .mui-switch-cp__box {
  border: 1px solid #DFDFDF;
}

[data-dpr="3"] .mui-switch,
[data-dpr="3"] .mui-switch-cp__box {
  border: 1.5px solid #DFDFDF;
}

.mui-switch:before,
.mui-switch-cp__box:before {
  content: " ";
  position: absolute;
  top: 0;
  left: 0;
  width: 1.166667rem;
  height: 0.5rem;
  border-radius: 0.25rem;
  background-color: #FDFDFD;
  transition: -webkit-transform 0.35s cubic-bezier(0.45, 1, 0.4, 1);
  transition: transform 0.35s cubic-bezier(0.45, 1, 0.4, 1);
  transition: transform 0.35s cubic-bezier(0.45, 1, 0.4, 1), -webkit-transform 0.35s cubic-bezier(0.45, 1, 0.4, 1);
}

.mui-switch:after,
.mui-switch-cp__box:after {
  content: " ";
  position: absolute;
  top: 0;
  left: 0;
  width: 0.5rem;
  height: 0.5rem;
  border-radius: 0.25rem;
  background-color: #FFFFFF;
  box-shadow: 0 0.016667rem 0.05rem rgba(0, 0, 0, 0.4);
  transition: -webkit-transform 0.35s cubic-bezier(0.4, 0.4, 0.25, 1.35);
  transition: transform 0.35s cubic-bezier(0.4, 0.4, 0.25, 1.35);
  transition: transform 0.35s cubic-bezier(0.4, 0.4, 0.25, 1.35), -webkit-transform 0.35s cubic-bezier(0.4, 0.4, 0.25, 1.35);
}

.mui-switch:checked,
.mui-switch-cp__input:checked ~ .mui-switch-cp__box {
  border-color: #ffe26d;
  background-color: #ffe26d;
}

.mui-switch:checked:before,
.mui-switch-cp__input:checked ~ .mui-switch-cp__box:before {
  -webkit-transform: scale(0);
  transform: scale(0);
}

.mui-switch:checked:after,
.mui-switch-cp__input:checked ~ .mui-switch-cp__box:after {
  -webkit-transform: translateX(0.666667rem);
  transform: translateX(0.666667rem);
}

.muivue-page-out-enter-active,
.muivue-page-out-leave-active,
.muivue-page-in-enter-active,
.muivue-page-in-leave-active {
  will-change: transform;
  height: 100%;
  width: 100%;
  position: absolute;
}

.muivue-page-out-enter-active {
  left: -100%;
  -webkit-animation: pageInLeft 0.5s ease-in forwards;
          animation: pageInLeft 0.5s ease-in forwards;
}

.muivue-page-out-leave-active {
  -webkit-animation: pageOutRight 0.5s ease-out forwards;
          animation: pageOutRight 0.5s ease-out forwards;
}

.muivue-page-in-enter-active {
  left: 100%;
  -webkit-animation: pageInRight 0.5s ease-in forwards;
          animation: pageInRight 0.5s ease-in forwards;
}

.muivue-page-in-leave-active {
  -webkit-animation: pageOutLeft 0.5s ease-out forwards;
          animation: pageOutLeft 0.5s ease-out forwards;
}

@-webkit-keyframes pageInLeft {
  0 {
    opacity: 0;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }

  100% {
    opacity: 1;
    -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0);
  }
}

@keyframes pageInLeft {
  0 {
    opacity: 0;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }

  100% {
    opacity: 1;
    -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0);
  }
}

@-webkit-keyframes pageOutLeft {
  0 {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }

  100% {
    opacity: 0;
    -webkit-transform: translate3d(-100%, 0, 0);
            transform: translate3d(-100%, 0, 0);
  }
}

@keyframes pageOutLeft {
  0 {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }

  100% {
    opacity: 0;
    -webkit-transform: translate3d(-100%, 0, 0);
            transform: translate3d(-100%, 0, 0);
  }
}

@-webkit-keyframes pageInRight {
  0 {
    opacity: 0;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }

  100% {
    opacity: 1;
    -webkit-transform: translate3d(-100%, 0, 0);
            transform: translate3d(-100%, 0, 0);
  }
}

@keyframes pageInRight {
  0 {
    opacity: 0;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }

  100% {
    opacity: 1;
    -webkit-transform: translate3d(-100%, 0, 0);
            transform: translate3d(-100%, 0, 0);
  }
}

@-webkit-keyframes pageOutRight {
  0 {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }

  100% {
    opacity: 0;
    -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0);
  }
}

@keyframes pageOutRight {
  0 {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }

  100% {
    opacity: 0;
    -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0);
  }
}

