.inner {
  -webkit-box-flex: 1;
  -ms-flex: 1;
      flex: 1;
  background-color: #fff;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
}

.inner .item a {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
}

.inner .item img {
  display: block;
  width: 0.666667rem;
  height: 0.666667rem;
  border: 0.016667rem solid #ddd;
}

.inner .item h3 {
  display: block;
  -webkit-box-flex: 1;
  -ms-flex: 1;
      flex: 1;
  width: 100%;
  max-height: 0.666667rem;
  overflow: hidden;
  line-height: 0.333333rem;
  margin: 0 0.166667rem;
  font-size: 0.266667rem;
}

.inner .item .date {
  display: block;
  height: 0.333333rem;
  line-height: 0.333333rem;
  color: #999;
}

.opacity {
  -webkit-animation: opacity 0.3s linear;
  animation: opacity 0.3s linear;
}

@-webkit-keyframes opacity {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes opacity {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}


